 export class InnerHtmlModel{
  public onChange
  public observer
  public timeout

   public innerHtmlModel
   public innerHtmlModelChange = new EventEmitter()

  constructor(public element:ElementRef){
    this.observer = new MutationObserver( ()=>this.setModel() )
    
    const config = {
      attributes: true,
      childList: true,
      characterData: true,
      subtree: true
    }
    this.observer.observe(this.element.nativeElement, config);

    //this.element.nativeElement.addEventListener("mouseup",()=>this.setModel())
    //this.element.nativeElement.addEventListener("keyup",()=>this.setModel())
  }

  ngOnChanges(){
    setTimeout(()=>this.setModel(), 0)
  }

  setModel(){
    this.innerHtmlModel = this.element.nativeElement.innerHTML
    this.innerHtmlModelChange.emit(this.innerHtmlModel)
  }

  ngOnDestroy(){
    this.observer.disconnect()
  }
static decorators: DecoratorInvocation[] = [
{ type: Directive, args: [{
  selector: '[innerHtmlModel]'
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: ElementRef, },
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'innerHtmlModel': [{ type: Input },],
'innerHtmlModelChange': [{ type: Output },],
};
}

 export class ElementHeightModel{
  public onResize
  public observer
  public timeout

   public elementHeightModel
   public elementHeightModelChange = new EventEmitter()

  constructor(public element:ElementRef){
    this.onResize = function(){
      this.setModel()
    }.bind(this)

    window.addEventListener('resize', this.onResize)
    setTimeout(()=>this.setModel(), 0)

    this.observer = new MutationObserver(()=>{
      this.setModel()
    })
    
    const config = {
      attributes: true,
      childList: true,
      characterData: true,
      subtree: true
    }
    this.observer.observe(this.element.nativeElement, config);
  }

  ngOnChanges(){
    setTimeout(()=>this.setModel(), 0)
  }

  setModel(){
    this.elementHeightModel = this.element.nativeElement.offsetHeight
    //this.element.nativeElement.style.border='1px solid red'
    this.elementHeightModelChange.emit(this.elementHeightModel)
  }

  ngOnDestroy(){
    this.observer.disconnect()
    window.removeEventListener(this.onResize)
  }
static decorators: DecoratorInvocation[] = [
{ type: Directive, args: [{
  selector: '[elementHeightModel]'
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: ElementRef, },
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'elementHeightModel': [{ type: Input },],
'elementHeightModelChange': [{ type: Output },],
};
}

